  .configuration-group__icon--email {
    top: 18px;
    right: 12px;
  }
  .configuration-group__comment {
    height: 175px;
  }
  .configuration-group--additional {
    display: grid;
    grid-template-rows: repeat(2, auto);
    grid-template-columns: repeat(2, auto);
  }
  .configuration-group__label--checkbox::before {
    width: 40px;
    height: 40px;
  }
  .configuration-group__label--checkbox::after {
    top: 13px;
    left: 10px;
    width: 24px;
    height: 19px;
  }
  .configuration-group--button {
    display: grid;
    grid-template-columns: repeat(2, auto);
    padding-top: 40px;
  }
  .configuration__footnote {
    margin-left: -250px;
  }
}
@media (min-width: 1440px) {
  .configuration-group {
    width: 100%;
  }
  .configuration__wrapper {
    display: grid;
    grid-template-columns: repeat(2, 570px);
    justify-content: space-between;
  }
  .configuration-group__subtitle {
    padding-left: 40px;
    margin-left: -40px;
  }
  .configuration-group--contact {
    display: grid;
    grid-template-columns: repeat(2, 570px);
    justify-content: space-between;
  }
  .configuration-group--additional {
    grid-template-rows: none;
    grid-template-columns: repeat(4, auto);
  }
  .configuration-group--button {
    justify-content: start;
    padding-top: 60px;
  }
  .configuration__footnote {
    margin-left: 75px;
  }
}
